
import React from 'react'
import { Badge, Container,  FormControl, Nav, Navbar,Dropdown,Button } from 'react-bootstrap'
import { AiFillDelete } from 'react-icons/ai';
import { Link } from 'react-router-dom';
import { CartState } from '../context/Context';

const Header = () => {
    const {state: { cart },dispatch} = CartState();
    return (
        <>
            <Navbar bg="dark" variant="dark" style={{ height: 70 }}>
                <Container>
                    <Navbar.Brand><Link to="/">Shoping Cart</Link></Navbar.Brand>
                    <Navbar.Text className="search">
                        <FormControl style={{ width: "500px" }}
                            type="search"
                            placeholder="Search a product"
                            className="ml-2"
                            aria-label="Search"
                        />
                    </Navbar.Text>
                    <Nav>
                        <Dropdown drop="start">
                            <Dropdown.Toggle variant="success">
                            <i className="fas fa-cart-plus" style={{color:"white", fontSize:"25px"}}></i>
                            <Badge>{cart.length}</Badge>
                            </Dropdown.Toggle>
                            <Dropdown.Menu style={{minWidth:"370px" , marginTop:"45px"}}>
                                 {cart.length>0 ? (<> {cart.map((prod)=>(<span className="cartItem" key={prod.id}>
                                  <img src={prod.image} className="cartItemImg" alt={prod.name} />
                                  <div className="cartItemDetail">
                                      <span>{prod.name} </span>
                                      <span>RS /- {prod.price.split(".")[0]} </span>
                                  </div>
                                  <AiFillDelete fontSize="20px" style={{cursor:"pointer"}}
                                  onClick={()=>dispatch({type:"REMOVE_FROM_CART", payload: prod,})} />
                                 </span>))}
                                 <Link to="/cart">
                                     <Button style={{width:"95%", margin:"0 10px"}}>Go to Cart</Button>
                                 </Link>
                                 </>) 

                                 : (<span style={{margin:"0 15px"}}>Cart is Empty !</span>)}
                                
                            </Dropdown.Menu>
                            
                        </Dropdown>
                    </Nav>

                </Container>

            </Navbar>
        </>
    )
}

export default Header;
