import React from 'react'
import { Button } from 'react-bootstrap'
import { CartState } from '../context/Context'
import Rating from './Rating'

const SingleProduct = ({ prod }) => {

    const { state: { cart }, dispatch, } = CartState();
    return (
        <>
            <div className="card my-3" >

                <img variant="light" src={prod.image} className="card-img-top" alt="..." />
                <div className="card-body">
                    <h5 className="card-title ">{prod.name}</h5>
                    <p className="card-text ">
                        <span>  RS  /-  {prod.price.split(".")[0]}</span>
                        {prod.fastDelivery ? (<div> Fast Delivery </div>) : (<div> 4 days Delivery </div>)}
                        <Rating rating={prod.ratings} />
                    </p>
                    <p className="card-text"><small className="text-muted"></small></p>
                    {
                        cart.some(p => p.id === prod.id) ?
                            (<Button onClick={() => { dispatch({ type: "REMOVE_FROM_CART", payload: prod, }); }} variant="danger">Remove from Cart</Button>)
                            : (<Button onClick={() => { dispatch({ type: "ADD_TO_CART", payload: prod, }); }}
                                disabled={!prod.inStock}>{!prod.inStock ? "Out of Stock" : "Add to Cart"}</Button>)
                    }



                </div>

            </div>
        </>
    )
}

export default SingleProduct
