import React from 'react'
import { BrowserRouter, Route } from 'react-router-dom'
import Cart from './component/Cart'
import Header from './component/Header'
import Home from './component/Home'

const App = () => {
  return (
   
      <BrowserRouter>
        <Header />
        <div>
          <Route path='/' exact >
            <Home />
          </Route>
          <Route path='/cart' exact >
            <Cart />
          </Route>
        </div>
      </BrowserRouter>
   
  )
}

export default App

