
import { AiFillStar, AiOutlineStar } from "react-icons/ai"

const Rating = ({rating, onClick ,sytle}) => {
    return (
        <>
            {[...Array(5)].map((_, index)=>(
                <span key={index} onClick={()=>onClick(index)} sytle={sytle}>
                    {rating>index ?(<AiFillStar style={{fontSize : "16px"}} />) : (<AiOutlineStar style={{fontSize : "16px"}} />)}
                </span>
            ))}
        </>
    )
}

export default Rating
